import React, { FC } from "react";
import { Wrapper, Item ,Site} from "./styled";


export interface ICustomBar {
    score: number
}
export const CustomBar: FC<ICustomBar> = ({ score }) => {
    const renderData = [
        {
            name: 'Early Emergent Reader',
            min: 300,
            max: 487,
            bgColor: '#69fcff',
        },
        {
            name: 'Late Emergent Reader',
            min: 488,
            max: 674,
            bgColor: '#00fbff',
        },
        {
            name: 'Transitional Reader',
            min: 675,
            max: 774,
            bgColor: '#00abb0',
        },
        {
            name: 'Probable Reader',
            min: 775,
            max: 900,
            bgColor: '#0c686b',
        }
    ]
    return (
        <Wrapper>
            {
                renderData.map((v, i) => (
                    <Item
                        key={i}
                        style={{
                            left: `${i * 150}px`,
                            height: `${(i + 1) * 60}px`,
                            backgroundColor: v.bgColor
                        }}
                    >
                        <div>{v.name}</div>
                        <div>SS&nbsp;{`${v.min}-${v.max}`}</div>
                        {
                            score >= v.min && score <= v.max &&
                            <Site >You are Here:{score}</Site>
                        }
                    </Item>
                ))
            }
        </Wrapper>
    )
}
