export async function upload({
  url,
  data,
}: {
  url: string;
  data: { [key: string]: any };
}) {
  const body = new FormData();
  Object.keys(data).forEach((key) => {
    body.append(key, data[key]);
  });
  const options: RequestInit = {
    method: "POST",
    body,
    credentials: "include",
  };
  return request({ url, options });
}
