import React, { useEffect } from "react";
import Pdf from "./Pdf";
import { useHistory, Switch, Route } from "react-router";
import { toObj, useScrollTop } from "../utils/common";

import "./antd.css";
import { setItem, StoreEnum } from "../utils/store";


const routerArr = [
  { path: `pdf`, component: Pdf },

];

export default () => {
  const history = useHistory();
  useScrollTop();

  useEffect(() => {
    const data = toObj();
    console.log(1111,data.csrftoken)
    setItem(StoreEnum.SESSION, data.csrftoken || '');
  }, [])

  return (
    <Switch>
      {routerArr.map((v, i) => (
        <Route key={i} path={`/${v.path}`} component={v.component} />
      ))}
    </Switch>
  );
};
